  <div class="bgChange headerPaddingIndex" :class="bgChange?'active':''">
    <header-div :bgColor="headerBgColor"></header-div>
    <banner-div :banners="banners"></banner-div>
    <product-line-div></product-line-div>
    <explore-div></explore-div>
    <esport-div></esport-div>
    <joinus-div></joinus-div>
    <footer-div></footer-div>
    <copyright-lang-div></copyright-lang-div>

    <subscribe-div  :isShowSubscribeDiv="isShowSubscribe"></subscribe-div>
  </div>
</template>

<script>

import HeaderTemplate from '../../template/common/header';
import BannerTemplate from '../../template/index/banner';
import IndexProductLineTemplate from '../../template/index/index_product_line';
import IndexExploreTemplate from '../../template/index/index_explore';
import IndexESportTemplate from '../../template/index/index_esport';
import IndexJoinMemberTemplate from '../../template/index/index_join_member';
import FooterTemplate from '../../template/common/footer';
import CopyrightAndLangTemplate from '../../template/common/copyright_lang';

import SubscribeTemplate from '../../template/common/subscribe';

require('@/assets/css/style.css');
export default {
  components: {
    'header-div' : HeaderTemplate,
    'banner-div' : BannerTemplate,
    'product-line-div' : IndexProductLineTemplate,
    'explore-div' : IndexExploreTemplate,
    'esport-div' : IndexESportTemplate,
    'joinus-div' : IndexJoinMemberTemplate,
    'footer-div' : FooterTemplate,
    'copyright-lang-div' : CopyrightAndLangTemplate,

    'subscribe-div' : SubscribeTemplate,
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  mounted:function(){
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  data() {
    return {
      headerBgColor: 'aorus-index-bgcolor',
      bgChange: false,
      banners:[
        {
          imageUrl: 'F_20200225151530ooGJ9q.jpg',
          title: 'Wallpaper',
          subTitle : 'AORUS ROBOT',
          btnText : 'Download',
          btnLink: 'https://tw.aorus.com/wallpaper.php?Dfile=upload/Downloads/Album/F_20200225151530ooGJ9q.JPG',
          fullLink: '',
          contentAlign: 'right', // left center right
          contentValign: 'top', // top middle bottom
          textAlign: 'right',
        },
        {
          imageUrl: 'B550-banner-1920x766.jpg',
          title: 'RISE UP',
          subTitle : 'AORUS B550 GAMING MOTHERBOARDS',
          btnText : 'SEE MORE',
          btnLink: 'https://www.aorus.com',
          fullLink: '',
          contentAlign: 'left',
          contentValign: 'middle',
          textAlign: 'left', 
        },
        {
          imageUrl: 'F_20200616172719eCVRE3.jpg',
          title: '',
          subTitle : '',
          btnText : '',
          btnLink: '',
          fullLink: 'https://www.aorus.com',
          contentAlign: 'right',
          contentValign: 'bottom',
          textAlign: 'left',
        },
        {
          imageUrl: 'ROBOT_2-final-3840x2160px.jpg',
          title: 'Wallpaper',
          subTitle : 'The King\'s Arrival',
          btnText : 'Download',
          btnLink: 'https://tw.aorus.com/wallpaper.php?Dfile=upload/Downloads/Album/F_20200226141137mCo1Ad.JPG',
          fullLink: '',
          contentAlign: 'left',
          contentValign: 'bottom',
          textAlign: 'left',
        },
        {
          imageUrl: 'F_20200629182419hk8gK7.jpg',
          title: '',
          subTitle : '',
          btnText : '',
          btnLink: '',
          fullLink: '',
          contentAlign: 'right',
          contentValign: 'bottom',
          textAlign: 'center',
        },
        {
          imageUrl: 'https://www.aorus.com/upload/F_20200723021256d5TCxl.JPG',
          title: '',
          subTitle : '',
          btnText : '',
          btnLink: '',
          fullLink: 'https://www.aorus.com/aorusnetwork',
          contentAlign: 'center',
          contentValign: 'middle',
          textAlign: 'center',
        },
        {
          imageUrl: 'https://www.aorus.com/upload/F_202007020916216pzHfl.JPG',
          title: '',
          subTitle : '',
          btnText : '',
          btnLink: '',
          fullLink: 'https://us.aorus.com/voiceofgamers',
          contentAlign: 'center',
          contentValign: 'middle',
          textAlign: 'center', 
        },
      ],
      isShowSubscribe: false
    }
  },
  methods: {
    handleScroll () {
      var explore_H = document.getElementById('explore').offsetTop;
      var explore_innerH = document.getElementById('explore').clientHeight/3 * 2;
      var window_H = window.innerHeight;
      if(window.scrollY > (explore_H - window_H + explore_innerH)){
        this.bgChange = true;
      }else{
        this.bgChange = false;
      }
      
      
      if(window.scrollY > (explore_H - window_H + document.getElementById('explore').clientHeight)){
        this.isShowSubscribe = true;
      }else{
        this.isShowSubscribe = false;
      }


      // console.log(event);

      var  templateTitle = document.getElementsByClassName('template-left-title');
      for(var j=0 ; j<templateTitle.length; j++){
        if(templateTitle[j].getBoundingClientRect().top < window.innerHeight/2*3 ){
          if(templateTitle[j].classList.contains('template-left-title-show')==false){
            templateTitle[j].classList.add('template-left-title-show');
          }
        }else{
          if(templateTitle[j].classList.contains('template-left-title-show')){
            templateTitle[j].classList.remove('template-left-title-show');
          }
        }
      }  

      var  element = document.getElementsByClassName('animate_class');
      for(var i=0 ; i<element.length; i++){
        var animateName = element[i].dataset.animate;
        var delay = element[i].dataset.animatedelay;
        if(element[i].getBoundingClientRect().top < window.innerHeight ){
          if(element[i].classList.contains('animate__animated')==false){
            element[i].classList.add('animate__animated',animateName);
            element[i].style.animationDelay = delay+'s';
          }
        }else{
          if(element[i].classList.contains('animate__animated')){
            element[i].classList.remove('animate__animated',animateName);
          }
        }
      } 
      
      
    }
  }
}
</script>

